.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 80px;
  padding: 20px;
  color: white;
  text-align: left;
}

.App-intro {
  font-size: large;
}

.top-buffer { margin-top:20px; }
.bottom-buffer { margin-bottom:20px; }
.bottom-buffer-lg { margin-bottom:100px; }
.right-buffer { margin-right:320px; }

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.slidecontainer {
    width: 350px; /* Width of the outside container */
}

/* The slider itself */
.slider {
    -webkit-appearance: none;  /* Override default CSS styles */
    -moz-appearance: none;
         appearance: none;
    width: 350px; /* Full-width */
    height: 25px; /* Specified height */
    background: #d3d3d3; /* Grey background */
    outline: none; /* Remove outline */
    opacity: 0.7; /* Set transparency (for mouse-over effects on hover) */
    -webkit-transition: .2s; /* 0.2 seconds transition on hover */
    -webkit-transition: opacity .2s;
    -o-transition: opacity .2s;
    transition: opacity .2s;
}

/* Mouse-over effects */
.slider:hover {
    opacity: 1; /* Fully shown on mouse-over */
}

/* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */ 
.slider::-webkit-slider-thumb {
    -webkit-appearance: none; /* Override default look */
    appearance: none;
    width: 0px; /* Set a specific slider handle width */
    height: 0px; /* Slider handle height */
    background: #4CAF50; /* Green background */
    cursor: pointer; /* Cursor on hover */
}

.slider::-moz-range-thumb {
    width: 0px; /* Set a specific slider handle width */
    height: 0px; /* Slider handle height */
    background: #4CAF50; /* Green background */
    cursor: pointer; /* Cursor on hover */
}

.loader {
    border: 3px solid #f3f3f3; /* Light grey */
    border-top: 3px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 30px;
    height: 30px;
    -webkit-animation: spin 2s linear infinite;
            animation: spin 2s linear infinite;
}

#grid-snap {
  width: 50px;
  height: 50px;
  background-color: #29e;
  color: #fff;
  font-size: 1.2em;
  border-radius: 4px;
  padding: 0px;
  margin: 2px;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.transcript-renderer {
  cursor: pointer;
  overflow:scroll;
  width:400px;
  height:300px;
}

.wrapper {
  text-align: left;
  width: 800px;
  height: 55px;
  background-color: #888;
  margin: 2px;
}

.wrapper-control {
  text-align: left;
  width: 800px;
  height: 20px;
  background-color: #bbb;
  margin-left: 2px;
  margin-bottom: 10px;
}

#aroll-control {
  width: 20px;
  height: 20px;
  margin: 0px;

  background-color: #f00;
  color: red;

  border-radius: 0.75em;
  padding: 0px;

  -webkit-transform: translate(0px, 0px);
          -ms-transform: translate(0px, 0px);
      transform: translate(0px, 0px);
}

div.inner > input {
    width: 1px;
    height:1px;
    z-index: 0;
    border:0;
    outline:0;
}

div.inner > span {
    width: 1px;
    height:1px;
    z-index: 1;
    background: white;
}











